.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo .image {
  width: 40px;
  height: 40px;
  display: block;
}

.header .nav__list {
  display: flex;
}

.header .nav__item {
  position: relative;
}

.header .nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  margin: 0 10px;
  font-family: inherit;
  font-size: 12px;
  line-height: 60px;
  transition: color .3s;
  display: block;
}

.header .nav__link:hover, .header .nav__link.is-active {
  color: #00acdc;
}

.header .nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

.container {
  grid-gap: 46px 48px;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: inherit;
  transition: transform .3s, color 1s;
  display: flex;
}

.card__image {
  justify-content: center;
  display: flex;
}

.card__logo {
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  margin: 40px 0 0;
  font-family: inherit;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-style: normal;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  align-items: flex-end;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  transition: color .3s, background-color .3s;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transition-duration: transform .3s;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #00acdc;
  transition: color .3s;
}

.price {
  font-style: normal;
  line-height: 18px;
}

.price--text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.price--number {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.reviews {
  text-align: right;
  color: #060b35;
  justify-content: flex-end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

body, ul {
  margin: 0;
}

.nav__list {
  padding: 0;
  list-style: none;
}

.nav__item:first-child a {
  margin-left: 0;
}

.nav__item:last-child a {
  margin-right: 0;
}

.nav__link {
  text-decoration: none;
}

.price {
  margin-top: 0;
  margin-bottom: 0;
}

.reviews {
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

